export class Type {
  static get Byte() {
    return 1
  }
  static get Word() {
    return Type.Byte * 2
  }
  static get DWord() {
    return Type.Word * 2
  }
  static get QWord() {
    return Type.DWord * 2
  }
  static get YWord() {
    return Type.QWord * 2
  }
  static Array(element: number, count: number) {
    return element * count
  }
  static Data(length: number) {
    return length
  }
}